<template>
  <div class="article">
    <time-clock :autoStart="autoStart" :sendSync="sendSync" :startTimer="startNow" :defaultVal="defaultVal" @getDataFromChild="getTimeClock"></time-clock>
    <van-skeleton
            title
            :row="8"
            :loading="loadingSkeleton" style="margin-top: 8vw">
      <div class="article-title">
        {{detail.article_title}}
      </div>
      <van-collapse v-model="activeNames">
        <van-collapse-item title="原文" name="1" size="large">
          <div class="article-content" v-html="detail.article_content"></div>
        </van-collapse-item>
        <van-collapse-item title="翻译" name="2" size="large">
          <div class="article-translate" v-html="detail.article_translate"></div>
        </van-collapse-item>
        <!--<van-icon class="iconfont icon-fenzu25"/> <van-icon name="gold-coin-o"/>-->
        <van-collapse-item name="3" size="large" v-if="showAnalysis">
          <div slot="title">解析</div>
          <div v-html="detail.article_analysis"></div>
        </van-collapse-item>
      </van-collapse>
      <div class="own-gap boxshadow-left" v-if="!showAnalysis">
        <van-row gutter="16" type="flex">
          <van-col span="12">
            <div class="price-title">
              解析付费：￥<span class="rmb-style">{{detail.pay_price}}</span> 元
            </div>
          </van-col>
          <van-col span="6"></van-col>
          <van-col span="6">
            <div class="buy-btn" @click="buyNow">付费查看</div>
            <!--<van-button :loading="loadingBtn" type="primary" size="normal" @click="buyNow" plain>查看讲解</van-button>-->
          </van-col>
        </van-row>
      </div>
    </van-skeleton>

    <!--<van-goods-action v-if="!showAnalysis">
      <div class="b-price-title">
        解析付费：￥<span class="rmb-style">{{detail.pay_price}}</span> 元
      </div>
      <van-goods-action-button
              type="primary"
              text="付费查看"
              @click="buyNow"
      />
    </van-goods-action>
-->
    <!--支付部分-->

    <!--取消支付后弹出-->
    <Confirm-dialog :confirmDialog="confirmOptions" @closeShow="closeConfirmDialog"></Confirm-dialog>

    <!--创建订单后弹出-->
    <Order-dialog :confirmDialog="createOrderOptions"></Order-dialog>

    <vue-fab style="bottom: 20%; right: 17%; z-index: 999"
            :fabAnimateBezier="fabAnimateBezier"
            :globalOptions="globalOptions"
            :mainBtnColor="mainBtnColor"
             size="big"
            @clickMainBtn="clickMainBtn">
      <fab-item style="top: -60px;" @clickItem="clickItem" color="#ff9900" icon="person" :idx="0" title="个人中心"></fab-item>
      <fab-item @clickItem="clickItem" color="#1cb5cc" icon="home" title="首页" :idx="1">
      </fab-item>
    </vue-fab>

  </div>
</template>
<script>
  import {mapGetters} from "vuex";
  import ConfirmDialog from '@/components/ConfirmDialog.vue'
  import timeClock from '@/components/Timer.vue'
  import OrderDialog from '@/components/OrderDialog.vue'
  import {NoticeBar, Panel, Skeleton, CollapseItem, Collapse, GoodsAction,
    GoodsActionIcon,
    GoodsActionButton} from 'vant';
  export default {
    name: 'article',
    components: {
      ConfirmDialog,
      OrderDialog,
      timeClock,
      [NoticeBar.name]: NoticeBar,
      [Skeleton.name]: Skeleton,
      [Panel.name]: Panel,
      [Collapse.name]: Collapse,
      [CollapseItem.name]: CollapseItem,
      [GoodsAction.name]: GoodsAction,
      [GoodsActionIcon.name]: GoodsActionIcon,
      [GoodsActionButton.name]: GoodsActionButton
    },
    computed: {
      ...mapGetters(["userInfo"])
    },
    data() {
      return {
        globalOptions: {spacing: 80, delay: 0.05},
        mainBtnColor: '#FF0099',
        fabAnimateBezier: 'ease-in-out', // ease 'ease' / 'linear' / 'ease-in' / 'ease-out' / 'ease-in-out' / '.18,.89,.91,.17'
        size: 'normal', // 'big' / 'normal' / 'small'
        loadingSkeleton: true,
        orderParams: {
          article: '',  // 5cd245a1ea21fd0efceed74b
          openid: '', // oAfgA1QTrvWbYXZ4iU2sGKMLEs5g
          pay_type: 1
        },
        payParams: {
          orderId: '',
          openId: ''
        },
        showAnalysis: false,
        orderId: '',
        activeNames: ['1'],
        detailParams: {
          id: '',
          openid: ''
        },
        fromUserOpenId: '',
        detail: '',
        loadingBtn: false,
        cancelPayShow: false,
        confirmOptions: {
          show: false,
          title: '还在犹豫？',
          msg: '联系督学班长了解下？',
          cancelText: '重新支付',
          confirmText: '跟她聊聊',
          cancelAction: () => {
            this.submitPay();
          },
          confirmAction: () => {
            this.$router.replace('/groupCode')
          }
        },
        createOrderOptions: {
          show: false,
          title: '提 示',
          msg: ' 该产品属于虚拟产品，一经购买，不支持退款或更换，请按需购买。',
          cancelText: '取 消',
          confirmText: '立即支付',
          cancelAction: () => {
            this.createOrderOptions.show = false;
          },
          confirmAction: () => {
            this.submitPay();
          }
        },
        // 定时器参数
        autoStart: false,
        sendSync: true,
        startNow: {
          start: false // 手动启动
        },
        readSeconds: 0, // 阅读时长s
        defaultVal: sessionStorage.getItem('timer') ? parseInt(sessionStorage.getItem('timer')) : 0 // 刷新后设置历史时间毫秒数
      }
    },
    created() {
      this.detailParams.openid = this.userInfo.openid;
      this.detailParams.id = this.$route.params.id;
      if(this.$route.query.fromU){
        this.fromUserOpenId = this.$route.query.fromU;
      }
    },
    mounted() {
      this.articleDetail();
      // 添加分享必要的参数
      let path = this.$router.history.current.path;
      // this.$router.push({ path, query: {fromU: this.userInfo.openid, key2: value2} });
      // 分享获取阅读权限
      let url = '';
      if(this.fromUserOpenId) {
        // 如果是分享过来的，替换为当前用户的openid
        url = location.origin + '' + location.pathname + '?' + 'fromU=' + this.userInfo.openid
      }else {
        url = location.href.split("#")[0] + '?fromU=' + this.userInfo.openid;
        // url = location.origin + '' + location.pathname + '?' + '/fromU=' +
      }
      this.wxShare.share(url, '微信公众号英文能力', `${this.userInfo.nickname}邀您一起坚持学习英语, Let's fighting!`, this.userInfo.headimgurl, 'payPage');
      // this.payParams.openId = localStorage.getItem('openid')
    },
    // 离开当前页面后执行
    destroyed: function () {
      console.log("我已经离开了！");
      this.stopTimer();
    },
    methods: {
      clickItem: function (idx) {
        if(idx.idx === 1) {
          this.$router.replace('/');
        }else {
          this.$router.replace('/center');
        }
      },
      clickMainBtn: function () {
        console.log('clickMainBtn')
      },
      // 清除session的定时, 顺便统计停留时间
      async stopTimer() {
        let readMin = (this.readSeconds/60).toFixed(2);
        let res = await this.apis.user.setReadSeconds({openid: this.detailParams.openid, readSeconds: readMin});
        sessionStorage.setItem('timer', '');
      },
      // 获取定时器数据存储在session
      getTimeClock(data) {
        this.readSeconds = data;
        sessionStorage.setItem('timer', parseInt(data));
      },
      articleDetail() {
        this.apis.article.getArticleDetail(this.detailParams).then(res => {
          this.detail = res.data;
          this.loadingSkeleton = false;
          this.startNow.start = true; // 开始计时
        })
      },
      articleAnalysis() {
        // 已购买的
        this.apis.order.articleDetail(this.payParams).then(res => {
          this.showAnalysis = true;
          this.detail = res.data.article;
          this.loadingSkeleton = false;
        })
      },
      closeConfirmDialog(val) {

      },
      // 购买解析部分内容
      buyNow() {
        let item =  this.detail;
        // this.loadingGoArticle()
        if (this.orderParams.article && item.id === this.orderParams.article) {
          this.createOrderOptions.show = true;
        } else {
          this.orderParams.article = item.id;
          this.orderParams.openid = this.detailParams.openid;
          // 创建订单
          this.apis.order.createArticleOrder(this, false, this.orderParams).then(res => {
            this.createOrderOptions.show = true;
            this.payParams.orderId = res.data.id;
            this.payParams.openId = res.data.openid;
          })
        }
      },
      // 发起支付
      submitPay() {
        this.createOrderOptions.show = false;
        this.confirmOptions.show = false;
        this.apis.order.articleDoPay(this, false, this.payParams).then(res => {
          this.$wx.chooseWXPay({
            timestamp: res.data.timeStamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
            nonceStr: res.data.nonceStr, // 支付签名随机串，不长于 32 位
            package: res.data.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=\*\*\*）
            signType: res.data.signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
            paySign: res.data.paySign, // 支付签名
            success: (res) => {
              // 支付成功后的回调函数
              this.articleAnalysis(this.payParams.orderId);
            },
            // 支付取消回调函数
            cancel: (res) => {
              console.log('用户取消支付~')
              // this.cancelPayShow = true;
              this.confirmOptions.show = true;
            },
            // 支付失败回调函数
            fail: (res) => {
              console.log('支付失败~')
            }
          })
        })
      },
    }
  }
</script>
<style lang="less">
  .article{
    margin-bottom: 20vw;
    text-align: left;
    .article-title {
      width: 100%;
      padding: 5vw 3vw;
      font-size: 4.2vw;
      font-weight: 600;
    }
    .article-content {
      width: 100%;
      /*border-radius: 2vw;*/
      /*padding: 1vw 2vw;*/
      /*border: 1px solid #1cb5cc;*/
    }
    .article-translate {

    }
    .own-gap{
      position: fixed;
      bottom: 0;
      padding: 3vw 3.8vw;
      width: 100%;
      background: #fff;
      .price-title{
        padding: 2vw 0;
        font-size: 3.8vw;
        .rmb-style{
          color: orangered;
        }
      }
      .buy-btn{
        line-height: 8vw;
        background: #f9a843;
        text-align: center;
        color: #fff;
        font-weight: 600;
      }
    }
    .b-price-title{
      padding: 10vw 4vw;
      height: 10vw;
      line-height: 9vw;
      font-size: 3.8vw;
      .rmb-style{
        color: orangered;
      }
    }
  }
</style>